.ToDoItem {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.ToDoItem-Text {
  width: 90%;
  background-color: white;
  border: 1px solid lightgrey;
  -webkit-box-shadow: 1px 1px 1px lightgrey;
          box-shadow: 1px 1px 1px lightgrey;
  padding: 12px;
  margin-right: 10px;
}

.ToDoItem-Delete {
  width: 35px;
  /*padding: 5px;*/
  height: 35px;
  cursor: pointer;
  background: #ff7373;
  border-radius: 10px;
  -webkit-box-shadow: 1px 1px 1px #c70202;
          box-shadow: 1px 1px 1px #c70202;
  color: white;
  font-size: 18px;
  margin-right: 5px;
}

.ToDoItem-Delete:hover {
  -webkit-box-shadow: none;
          box-shadow: none;
  margin-top: 1px;
  margin-left: 1px;
}



